@import "https://fonts.googleapis.com/css2?family=Jost:wght@100;300;500&display=swap";
* {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  color: #5a5a5a;
  text-decoration: none;
}

body {
  background: #e2e1da;
}

.first-page {
  width: 90vw;
  height: 100vh;
  margin-top: 20px;
  margin-left: 5vw;
}

.first-page .top {
  color: #5a5a5a;
  justify-content: space-between;
  font-family: Jost, sans-serif;
  font-size: 20px;
  font-weight: 300;
  display: flex;
}

.first-page .top a {
  padding-left: 70px;
  font-weight: 300;
}

.title {
  width: 90vw;
  position: absolute;
  bottom: 8vh;
  overflow-x: hidden;
}

.title #title-main {
  width: 90vw;
  animation: 1.5s reveal;
}

.title #marquee-background {
  width: 90vw;
  height: 26pt;
  background: #424242;
  margin-top: 10pt;
}

.title #title-marquee {
  color: #e7ebe8;
  margin-top: 10pt;
  font-family: Jost, sans-serif;
  font-size: 1.5em;
  transform: translateY(-35pt);
}

.title .lines #line1 {
  border-bottom: 20px solid #424242;
  animation: 2s forwards line-reveal;
}

.title .lines #line2 {
  border-bottom: 10px solid #424242;
  margin-top: 10px;
  animation: 2.2s forwards line-reveal;
}

.title .lines #line3 {
  border-bottom: 5px solid #424242;
  margin-top: 10px;
  animation: 2.4s forwards line-reveal;
}

.title .lines #line4 {
  border-bottom: 2.5px solid #424242;
  margin-top: 10px;
  animation: 2.6s forwards line-reveal;
}

.title .lines #line5 {
  border-bottom: 1.25px solid #424242;
  margin-top: 10px;
  animation: 2.8s forwards line-reveal;
}

.title .mask-container {
  display: flex;
}

.title .mask {
  width: 100%;
  height: 12vw;
  background: #2d2d2d;
  animation: 2s forwards mask-reveal;
  position: absolute;
}

.main {
  width: 90vw;
  margin-top: 0;
  margin-left: 5vw;
  padding-bottom: 70pt;
  font-family: Jost, sans-serif;
  display: flex;
}

.main .col-left {
  width: 45vw;
  margin-right: 5vw;
}

.main .col-left img {
  width: 45vw;
  height: 45vw;
  object-fit: cover;
}

.main .text {
  color: #4e4e4e;
  font-size: 30px;
}

@keyframes reveal {
  from {
    clip-path: polygon(0% 0%, 0% 0%, 0% 100%, 0% 100%);
  }

  to {
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  }
}

@keyframes mask-reveal {
  0% {
    clip-path: polygon(0% 0%, 0% 0%, 0% 100%, 0% 100%);
  }

  50% {
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  }

  100% {
    clip-path: polygon(100% 0%, 100% 0%, 100% 100%, 100% 100%);
  }
}

@keyframes line-reveal {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

