@import url('https://fonts.googleapis.com/css2?family=Jost:wght@100;300;500&display=swap');

*{
    padding: 0;
    margin: 0;
}

ul{
    list-style-type: none;
}

a{
    text-decoration: none;
    color: #5a5a5a;
}

body{
    background: #e2e1da;
}

.first-page{
    width: 90vw;
    height: 100vh;
    margin-left: 5vw;
    margin-top: 20px;

    .top{
        font-family: 'Jost', sans-serif;
        font-weight: 300;
        font-size: 20px;
        
        color: #5a5a5a;
        display: flex;
        justify-content: space-between;

        a{
            padding-left: 70px;
            font-weight: 300;
        }
    }
}

.title{
    width: 90vw;
    position: absolute;
    bottom: 8vh;
    // margin-top: 60vh;
    
    overflow-x: hidden;

    #title-main{
        width: 90vw;
        animation: 1.5s reveal;
    }

    #marquee-background{
        margin-top: 10pt;
        width: 90vw;
        height: 26pt;
        background: #424242;
    }

    #title-marquee{
        font-family: 'Jost', sans-serif;
        transform: translateY(-35pt);
        font-size: 1.5em;
        margin-top: 10pt;
        color: #e7ebe8;
        // color: #424242;
        // -webkit-text-stroke: .1px white;
    }


    .lines{

        #line1{
            border-bottom: 20px solid #424242;
            animation: 2s line-reveal forwards;
        }

        #line2{
            margin-top: 10px;
            border-bottom: 10px solid #424242;
            animation: 2.2s line-reveal forwards;
        }
        #line3{
            margin-top: 10px;
            border-bottom: 5px solid #424242;
            animation: 2.4s line-reveal forwards;
        }
        #line4{
            margin-top: 10px;
            border-bottom: 2.5px solid #424242;
            animation: 2.6s line-reveal forwards;
        }
        #line5{
            margin-top: 10px;
            border-bottom: 1.25px solid #424242;
            animation: 2.8s line-reveal forwards;
        }
       
    }

    .mask-container{
        display: flex;
        // background: black;
    }
    
    .mask{
        position: absolute;
        width: 100%;
        height: 12vw;
        background: #2d2d2d;
        animation: 2s mask-reveal forwards;
    }

    
}

.main{
    font-family: 'Jost', sans-serif;
    width: 90vw;
    margin-left: 5vw;
    margin-top: 0pt;

    display: flex;
    padding-bottom: 70pt;

    .col-left{
        width: 45vw;

        img{
            width: 45vw;
            height: 45vw;
            object-fit: cover;
        }

        margin-right: 5vw;
    }

    .text{
        // margin-top: 10pt;
        font-size: 30px;
        color: #4e4e4e;
        
    }
}

@keyframes reveal{
    from{
        clip-path: polygon(0% 0%, 0% 0%, 0% 100%, 0% 100%);
    }
    to{
        clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
    }
}

@keyframes mask-reveal{
    0%{
        clip-path: polygon(0% 0%, 0% 0%, 0% 100%, 0% 100%);
    }
    50%{
        clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
    }
    100%{
        clip-path: polygon(100% 0%, 100% 0%, 100% 100%, 100% 100%);
    }
}

@keyframes line-reveal{
    0%{
        width: 0;
    }
    100%{
        width: 100%;
    }
}
